import React, { useState } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
  CAlert,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { cilLockLocked, cilUser } from "@coreui/icons";
import axios from "axios";

const LoginForm = (props) => {
  const [login, setLogin] = useState("");
  const [password, setPassword] = useState("");
  const [validated, setValidated] = useState(false);
  const [visibleError, setVisibleError] = useState(false);

  const handleSubmit = async (event) => {
    event.preventDefault();

    const form = event.currentTarget;
    const ok = form.checkValidity() !== false;
    setValidated(true);
    if (!ok) return;

    try {
      setVisibleError(false);

      const body = {
        username: login,
        password: password,
      };

      // ✅ Django JWT login (на цьому ж домені)
      const res = await axios.post("/api/auth/login/", body, {
        headers: { "Content-Type": "application/json" },
      });

      // simplejwt -> access/refresh
      if (!res?.data?.access) {
        setVisibleError(true);
        return;
      }

      localStorage.setItem("access_token", res.data.access);
      localStorage.setItem("refresh_token", res.data.refresh || "");

      // ✅ отримати дані користувача (на цьому ж домені)
      const meRes = await axios.get("/api/users/me/", {
        headers: {
          Authorization: `Bearer ${res.data.access}`,
        },
      });

      localStorage.setItem("user_data", JSON.stringify(meRes.data));

      if (props.redirect !== undefined) {
        window.location.reload();
      } else {
        window.location.replace("/");
      }
    } catch (e) {
      console.log("LOGIN ERROR:", e);
      setVisibleError(true);
    }
  };

  return (
    <CCard className="p-4">
      <CCardBody>
        <CAlert
          color="danger"
          dismissible
          visible={visibleError}
          onClose={() => setVisibleError(false)}
        >
          Помилка входу! Перевірте введені дані.
        </CAlert>

        <CForm
          onSubmit={handleSubmit}
          className="needs-validation"
          noValidate
          validated={validated}
        >
          <h1>Увійти</h1>
          <p className="text-medium-emphasis">Вхід в кабінет</p>

          <CInputGroup className="mb-3">
            <CInputGroupText>
              <CIcon icon={cilUser} />
            </CInputGroupText>
            <CFormInput
              placeholder="Логін"
              autoComplete="username"
              value={login}
              onChange={(e) => setLogin(e.target.value)}
              required
            />
          </CInputGroup>

          <CInputGroup className="mb-4">
            <CInputGroupText>
              <CIcon icon={cilLockLocked} />
            </CInputGroupText>
            <CFormInput
              type="password"
              placeholder="Пароль"
              autoComplete="current-password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </CInputGroup>

          <CRow>
            <CCol xs={6}>
              <CButton type="submit" color="primary" className="px-4">
                Увійти
              </CButton>
            </CCol>
          </CRow>
        </CForm>
      </CCardBody>
    </CCard>
  );
};

export default LoginForm;
